import * as React from "react"

import Layout from "../layout"

import PhotoEmpty from "../../images/photo_empty.svg"
import { Link } from "gatsby"
import { Awards, compareStrings, CompetitionPriority } from "../../utils"

const CompetitorsPage = ({ pageContext: { competitors } }) => {
  const importantCompetitionsFilter = it =>
    !["regional_os", "final_os"].includes(it.priority) &&
    it.place >= 1 &&
    it.place <= 3

  const sortByCompetitionPriority = (a, b) =>
    CompetitionPriority[b.priority] - CompetitionPriority[a.priority] ||
    b.year - a.year

  const AwardIcon = ch => (
    <Link
      key={ch.id}
      className="flex-none bg-gray-50 hover:bg-gray-100 shadow-sm hover:shadow-inner cursor-pointer rounded-full p-2 transition-all"
      to={ch.link}
      title={ch.name}
    >
      <img
        src={Awards.hasOwnProperty(ch.place) ? Awards[ch.place] : Awards[0]}
        alt={ch.name}
        width={25}
        height={25}
        className="inline-block"
      />
    </Link>
  )
  return (
    <Layout showbanner={false} title="Сборная СЦК">
      <h1 className="text-3xl font-bold mb-6">Сборная СЦК</h1>
      <div className="grid gap-3 grid-cols-1 md:grid-cols-2 justify-items-center items-stretch">
        {competitors
          .sort((a, b) => {
            const ratingMap = it =>
              (5 - it.place) *
              (it.priority === "final" ? 6 : CompetitionPriority[it.priority])
            const ratingFilter = it => it.place >= 1 && it.place <= 4
            const ratingA = a.competitions
              .filter(ratingFilter)
              .map(ratingMap)
              .reduce((a, b) => a + b, 0)
            const ratingB = b.competitions
              .filter(ratingFilter)
              .map(ratingMap)
              .reduce((a, b) => a + b, 0)
            return (
              ratingB - ratingA ||
              a.hasOwnProperty("photo") - b.hasOwnProperty("photo") ||
              compareStrings(a.name, b.name)
            )
          })
          .map(c => {
            let competitions = c.competitions.filter(
              importantCompetitionsFilter
            )
            if (competitions.length === 0) competitions = c.competitions
            return (
              <div
                key={c.name}
                className="flex gap-4 items-center bg-white border-2 rounded-lg drop-shadow-xl p-6 w-full"
              >
                <div className="person_photo flex-none">
                  <img
                    src={c.photo || PhotoEmpty}
                    alt={c.name}
                    style={!c.photo ? { objectPosition: "50% 0%" } : {}}
                  />
                </div>
                <div>
                  <h3 className="text-xl font-semibold mb-4">{c.name}</h3>
                  <div className="flex gap-2 flex-wrap">
                    {competitions
                      .sort(sortByCompetitionPriority)
                      .map(AwardIcon)}
                  </div>
                </div>
              </div>
            )
          })}
      </div>
    </Layout>
  )
}

export default CompetitorsPage
